<template>
  <div class="gobolino">
    <div v-if="spotIndex < 0" class="cat-intro">
      <div class="cats">
        <img src="img/pegintro.jpg" alt="Image of Peg sticking her tongue out" />
        <img src="img/kipintro.jpg" alt="Image of Kip with a glass of wine" />
      </div>
      <h1>{{ whiskers.title }}</h1>
      <p>{{ whiskers.intro }}</p>
      <p>Ever wondered if you're more Peg than Kip? Science has finally found a way to help you out. Answer the following questions honestly and discover which feline you most resemble.</p>
      <p>Don't delay - find out today.</p>
      <p>
        <button class="start" @click="startQuiz">Start quizzz, human</button>
      </p>
    </div>

    <div v-else>
      <div v-for="(question, index) in whiskers.questions" :key="index">
        <div v-show="index === spotIndex">
          <h2>{{ question.catq }}</h2>
          <ul>
            <li v-for="option in question.options" :key="option.value">
              <label>
                <input
                  type="radio"
                  v-bind:value="option.value"
                  v-bind:name="index"
                  v-model="paws[index]"
                />
                {{ option.text }}
              </label>
            </li>
          </ul>
          <!-- Show a message if no answer has been selected -->
          <div class="messages">
            <p v-if="message !== ''" class="message">{{ message }}</p>
          </div>
          <div class="controls">
            <button class="back" v-if="spotIndex > 0" @click="slinkBack" v-html="`<< Back`"></button>
            <button
              class="fwd"
              v-if="spotIndex !== whiskers.questions.length-1"
              @click="jumpFwd"
            >Fwd >></button>
            <button
              class="submit"
              v-if="spotIndex === whiskers.questions.length-1"
              @click="commonHousehold"
            >Which cat am I?</button>
          </div>
        </div>
      </div>
    </div>
    <!-- Only show the results at the end of the cat quiz -->
    <div class="results" v-show="spotIndex === whiskers.questions.length">
      <h2>Results</h2>
      <!-- <div v-if="spotIndex === whiskers.questions.length"> -->
      <div v-if="spotIndex === whiskers.questions.length">
        <!-- {{ commonHousehold }} -->
        <!-- Which cat are you? -->
        <div class="kip" v-if="yourCat === 'kip'">
          <h2>You are Kip!</h2>
          <img src="img/kip.jpg" alt />
          <p>You are a cool customer with a big personality who takes the world in their stride. Describing you as "relaxed" doesn't really do you justice - in fact, you are so chilled out you often fall right off of the furniture. I guess that's another point to make - you're not exactly the sharpest tool in the box, and can be fooled quite easily, but you make up for this by being extremely loveable.</p>
          <p>If there's a party going on, you're right there in the heart of the action, collecting everyone's affection and generally being the centre of attention. You've got plenty of confidence and are fully aware that everyone loves you. You're a bit like a cat celebrity really. Maybe one day they'll make a film all about you, but until then carry on being your bold and brilliant self. We'll keep on watching your every move.</p>
          <div class="social">
            <h3>Spread the word</h3>
            <ul>
              <li>
                <a
                  href="https://twitter.com/intent/tweet?text=I%20was%20Kip%20in%20the%20%22Which%20of%20my%20cats%20are%20you%3F%22%20quiz.%20Which%20cat%20are%20you%3F%20https://russelljkennedy.github.io/catquiz/"
                  target="_blank"
                >
                  <i class="fab fa-twitter"></i> Twitter
                </a>
              </li>
              <li>
                <a
                  href="https://www.facebook.com/sharer/sharer.php?u=https%3A//russelljkennedy.github.io/catquiz/"
                  target="_blank"
                >
                  <i class="fab fa-facebook"></i> Facebook
                </a>
              </li>
              <li>
                <a
                  href="https://pinterest.com/pin/create/button/?url=https%3A//russelljkennedy.github.io/catquiz/&media=https%3A//russelljkennedy.github.io/catquiz/img/kip.jpg&description=I%20was%20Kip%20in%20the%20%22Which%20of%20my%20cats%20are%20you%3F%22%20quiz.%20Which%20cat%20are%20you%3F%20"
                  target="_blank"
                >
                  <i class="fab fa-pinterest"></i> Pinterest
                </a>
              </li>
            </ul>
          </div>
        </div>
        <div class="peg" v-if="yourCat === 'peg'">
          <h2>You are Peg!</h2>
          <img src="img/peg.jpg" alt />
          <p>You live in a state of perpetual fear, and are constantly looking over your shoulder for potential traps. You love to eat and enjoy nothing better than a good scratch. Unfortunately you take these pleasures to the extreme, and sometimes overeat, over-scratch and over-groom.</p>
          <p>If people are nearby, you are likely to hide from them as - let's face it - they're probably trying to trap you. However, once someone has your trust you are excellent company with a tuneful purr. Basically, you are awesome, but you don't know it - if only you had the self-awareness to realise that you are a totally terrific tortoiseshell. We love you, Peg!</p>
          <div class="social">
            <h3>Spread the word</h3>
            <ul>
              <li>
                <a
                  href="https://twitter.com/intent/tweet?text=I%20was%20Peg%20in%20the%20%22Which%20of%20my%20cats%20are%20you%3F%22%20quiz.%20Which%20cat%20are%20you%3F%20https://russelljkennedy.github.io/catquiz/"
                  target="_blank"
                >
                  <i class="fab fa-twitter"></i> Twitter
                </a>
              </li>
              <li>
                <a
                  href="https://www.facebook.com/sharer/sharer.php?u=https%3A//russelljkennedy.github.io/catquiz/"
                  target="_blank"
                >
                  <i class="fab fa-facebook"></i> Facebook
                </a>
              </li>
              <li>
                <a
                  href="https://pinterest.com/pin/create/button/?url=https%3A//russelljkennedy.github.io/catquiz/&media=https%3A//russelljkennedy.github.io/catquiz/img/peg.jpg&description=I%20was%20Kip%20in%20the%20%22Which%20of%20my%20cats%20are%20you%3F%22%20quiz.%20Which%20cat%20are%20you%3F%20"
                  target="_blank"
                >
                  <i class="fab fa-pinterest"></i> Pinterest
                </a>
              </li>
            </ul>
          </div>
        </div>
        <div class="reset">
          <p>
            <button class="reload" @click="newMew">Start again</button>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import catq from "../assets/whiskers.json";
export default {
  name: "Quiz",
  data: () => ({
    spotIndex: -1,
    paws: [],
    message: "",
    mode: 0,
    yourCat: ""
  }),
  methods: {
    // Only show the intro at the start of the quiz, when the spotIndex is set to -1
    startQuiz: function() {
      this.spotIndex++;
    },
    // Forward and back functions
    jumpFwd: function() {
      // Has the question been answered?
      if (this.paws.length == this.spotIndex) {
        this.message = "I can hazz answer?...";
      } else {
        this.message = "";
        this.spotIndex++;
      }
    },
    slinkBack: function() {
      this.message = "";
      this.spotIndex--;
    },
    // Find the mode and end the quiz
    commonHousehold: function() {
      this.message = "";
      this.spotIndex++;
      window.scrollTo(0, 0);

      for (let i = 0; i < this.paws.length; i++) {
        let tempEl = this.paws[i];
        let tempCt = 0;
        for (let j = 0; j < this.paws.length; j++) {
          if (this.paws[j] == tempEl) {
            tempCt++;
          }
          if (tempCt > 0) {
            this.mode = tempEl;
          }
        }
        if (this.mode == 1 || this.mode == 2) {
          this.yourCat = "kip";
        } else {
          this.yourCat = "peg";
        }
      }
    },
    // Reset quiz
    newMew: function() {
      this.spotIndex = -1;
      this.paws = [];
      this.mode = 0;
      this.yourCat = "";
      window.scrollTo(0, 0);
    }
  },

  computed: {
    whiskers() {
      // Return entire json file
      return catq;
    }
  }
};
</script>
